.header {
  position: relative;
  background-color: #FFFFFF;
}

.header__logo {
  width: 320px;
  height: 79px;
  box-shadow: 0px 1px 0px #ECECEC, inset 0px 1px 0px #ECECEC;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__button {
  position: absolute;
  right: 30px;
  top: 29px;
  width: 21px;
  height: 20px;
  background-image: url("../../img/menu.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__button--menu-opened {
  background-image: url("../../img/menu-close.svg");
}

.menu {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}

.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__item {
  box-sizing: border-box;
  padding-left: 76px;
  width: 320px;
  height: 79px;
  box-shadow: 0px 1px 0px #ECECEC, inset 0px 1px 0px #ECECEC;
}

.menu__item--search {
  background-image: url("../../img/search.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
}

.menu__item--cart {
  background-image: url("../../img/cart.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
}

.menu__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  display: block;
  box-sizing: border-box;
  width: 100%;
  min-height: 52px;
  background: #FFFFFF;
  border: 2px solid #444444;
  cursor: pointer;
}
.button--form {
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
}

.promo {
  background-color: #ffffff;
  background-image: url("../../img/hero-mobile@2x 1.jpg");
  background-position: top -15px center;
}

.promo__title-wrapper {
  padding: 0 30px;
  padding-top: 50px;
}

.promo__title {
  color: #000000;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 120px;
}

.promo-block {
  height: 103px;
  background-color: #63D1BB;
  background-image: url("../../img/interior.svg");
  background-position: right 40px center;
  background-repeat: no-repeat;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  display: flex;
  align-items: center;
  padding: 0 30px;
}
.promo-block--dark {
  background-color: #56C3B2;
  background-image: url("../../img/toy.svg");
  background-position: right 30px center;
}

.promo-block__name {
  margin: 0;
  max-width: 30px;
  padding-bottom: 10px;
}

.product {
  font-family: "Open Sans";
  font-style: normal;
  padding: 0 30px;
  padding-top: 32px;
  padding-bottom: 40px;
  background-color: #ffffff;
  background-image: url("../../img/zigzag-line-desktop.svg");
  background-repeat: no-repeat;
  background-position: bottom;
}

.product__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  margin-bottom: 4px;
}

.product__subtitle {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63D1BB;
  margin: 0;
  margin-bottom: 18px;
}

.product__desc {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0;
  margin-bottom: 20px;
}

.product-info {
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  display: block;
  margin-bottom: 28px;
}

.product-info tbody {
  display: block;
}

.product-info__row {
  display: block;
  box-sizing: border-box;
  height: 40px;
  border: 0;
  border-bottom: 1px solid #ECECEC;
  padding: 0;
  margin: 0;
  margin-left: -3px;
}

.product-info__row td {
  border: 0;
  padding: 0;
  margin: 0;
}

.product-info__column {
  vertical-align: middle;
  width: 97px;
  height: 33px;
}

.product__image {
  margin-bottom: 22px;
}

.product__price {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
  margin: 0;
  margin-bottom: 23.5px;
}

.features {
  font-family: "Open Sans";
  font-style: normal;
  background-color: #ffffff;
  padding: 0 30px;
  padding-top: 33.5px;
  padding-bottom: 15px;
}

.features__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  margin-bottom: 27px;
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.feature {
  list-style: none;
  margin-bottom: 30px;
}

.feature__icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 39px;
  margin-bottom: 14px;
  padding-left: 4px;
}

.feature__desc {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
  margin: 0;
}

.reviews {
  padding: 0 30px;
  padding-top: 15px;
  padding-bottom: 48px;
  background-color: #ffffff;
  background-image: url("../../img/zigzag-line-desktop.svg");
  background-repeat: no-repeat;
  background-position: bottom;
}

.reviews__title {
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  margin-bottom: 14px;
}

.review {
  margin-bottom: 30px;
}

.review__text {
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  margin: 0;
  margin-bottom: 33px;
}

.review__author {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  margin: 0;
  margin-bottom: 3px;
}

.review__instagram {
  font-weight: 400;
  font-size: 17px;
  line-height: 30px;
  color: #444444;
  margin: 0;
}

.slider__controls {
  display: flex;
}

.slider__button {
  display: block;
  width: 50%;
  box-sizing: border-box;
  height: 52px;
  background-color: #FFFFFF;
  border: 1px solid #ECECEC;
  background-repeat: no-repeat;
  background-position: center;
}
.slider__button--backward {
  background-image: url("../../img/huge-arrow-left.svg.svg");
}
.slider__button--forward {
  background-image: url("../../img/huge-arrow-right.svg.svg");
}

.reviews__slider {
  margin-bottom: 39px;
}

.contacts {
  font-family: "Open Sans";
  font-style: normal;
  padding: 0 30px;
  padding-top: 39px;
  padding-bottom: 40px;
  background-color: #ffffff;
}

.contacts__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  margin-bottom: 28px;
}

.contacts__info {
  margin-bottom: 63px;
}

.contacts__item {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
  display: flex;
  align-items: top;
  justify-content: space-between;
  padding-right: 3px;
  margin-bottom: 30px;
}

.contacts__mail {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
}

.contacts__mail-text {
  padding-right: 12px;
  padding-bottom: 2px;
  border-bottom: 1px solid #62D1BA;
}

.contacts__address {
  display: inline-block;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
}

.contacts__map {
  height: 454px;
  width: 320px;
  margin-left: -30px;
  background-image: url("../../img/map-pin.svg"), url("../../img/map-mobile@1x 1.png");
  background-repeat: no-repeat;
  background-position: left 138px top 145px, center;
  margin-bottom: 41px;
}

.contacts__button {
  letter-spacing: 0.3px;
}

.footer {
  background-color: #F1F1F1;
  padding: 23px 30px 10px 30px;
}

.footer__logo {
  display: none;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  min-width: 140px;
  border-bottom: 1px solid #E5E5E5;
  margin-bottom: 10px;
}

.socials__item {
  width: 25px;
  height: 30px;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 27px;
}
.socials__item--instagram {
  background-image: url("../../img/insta.svg");
}
.socials__item--facebook {
  background-image: url("../../img/fb.svg");
}
.socials__item--twitter {
  background-image: url("../../img/twitter.svg");
}
.socials__item:not(:last-child) {
  margin-right: 35px;
}

.creator {
  display: flex;
  justify-content: center;
  align-items: center;
}

.creator__desc {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
  text-align: center;
  margin-right: 27px;
}

.content__title-wrapper {
  background-color: #F1F1F1;
  padding: 73.5px 30px;
}

.content__title {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  text-align: center;
  margin: 0;
}

.form {
  font-family: "Open Sans";
  padding: 0 30px;
  padding-top: 17px;
  padding-bottom: 57px;
  background-color: #ffffff;
}

.form__desc {
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: #444444;
  margin: 0;
  margin-bottom: 30px;
}

.form__item {
  border: none;
  margin: 0;
  padding: 0;
}
.form__item:not(:last-of-type) {
  margin-bottom: 33px;
}

.form__name {
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #63D1BB;
  white-space: nowrap;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-left: -2px;
}
.form__name::after {
  content: "";
  display: block;
  width: 100%;
  max-width: 200px;
  height: 1px;
  margin-left: 15px;
  background-color: #62D1BA;
}

.form__name--type {
  margin-bottom: 31px;
}

.form__name--color {
  margin-bottom: 25px;
}

.form__name--fio {
  margin-bottom: 28px;
}

.form__name--tel {
  margin-bottom: 7px;
}

.form__name--comment {
  margin-bottom: 24px;
}

.form__name--mail {
  margin-bottom: 5px;
}

.form__label {
  display: block;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  user-select: none;
  cursor: pointer;
}

.form__radio:not(:last-child) {
  margin-bottom: 12px;
}

.form__checkbox:not(:last-child) {
  margin-bottom: 23px;
}

.form__input:not(:last-child) {
  margin-bottom: 22px;
}

.form__textarea {
  margin-bottom: 16px;
}

.form__footnote {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
  margin: 0;
  margin-bottom: 30px;
}

.radio__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.radio__label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  user-select: none;
  cursor: pointer;
}

.radio__label::before {
  content: "";
  box-sizing: border-box;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #444444;
  flex-shrink: 0;
  margin-right: 20px;
}

.radio__input:checked + .radio__label::after {
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  position: absolute;
  top: 11px;
  left: 12px;
  background-color: #62D1BA;
}

.checkbox__input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox__label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  user-select: none;
  cursor: pointer;
}

.checkbox__label::before {
  content: "";
  box-sizing: border-box;
  width: 38px;
  height: 38px;
  border: 2px solid #444444;
  flex-shrink: 0;
  margin-right: 24px;
}

.checkbox__input:checked + .checkbox__label::after {
  content: "";
  box-sizing: border-box;
  width: 22px;
  height: 11px;
  border-left: 2px solid #444444;
  border-bottom: 2px solid #444444;
  transform: rotate(-45deg);
  position: absolute;
  top: 11px;
  left: 9px;
}

.input {
  display: block;
  height: 52px;
  box-sizing: border-box;
  width: 100%;
  border: none;
  background-color: #FFFFFF;
  box-shadow: inset 0px -2px 0px #000000;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 19px;
  line-height: 18px;
  color: #444444;
}
.input::placeholder {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  opacity: 0.3;
}
.input--phone {
  padding-bottom: 8px;
  background-image: url("../../img/phone.svg");
  background-repeat: no-repeat;
  background-position: right 7px top 12px;
}
.input--mail {
  padding-bottom: 8px;
  background-image: url("../../img/mail.svg");
  background-repeat: no-repeat;
  background-position: right 2px center;
}
.input--textarea {
  background: #FFFFFF;
  border: 2px solid #444444;
  box-sizing: border-box;
  box-shadow: none;
  min-height: 142px;
  padding: 17px;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
body {
  width: 320px;
  background-color: #000000;
  margin: 0 auto;
  font-family: "Open Sans";
}